// jest.setup.ts
// @ts-nocheck

// 글로벌 설정
global.__DEV__ = true;
global.window = global.window || {};

// 모킹 설정
jest.mock('react-native', () => ({
  StyleSheet: {
    create: jest.fn(styles => styles),
    flatten: jest.fn(style => style),
  },
  Platform: {
    OS: 'android',
    select: jest.fn(obj => obj.android || obj.default),
  },
  Dimensions: {
    get: jest.fn(() => ({ width: 375, height: 667 })),
  },
  View: 'View',
  Text: 'Text',
  TouchableOpacity: 'TouchableOpacity',
  Image: 'Image',
  ScrollView: 'ScrollView',
  TurboModuleRegistry: {
    getEnforcing: jest.fn(() => ({
      show: jest.fn(),
      debugRemotely: jest.fn(),
    })),
  },
  Alert: {
    alert: jest.fn()
  },
}));

// Testing-library 모킹 제거 - 오류의 원인이 됨
// jest.mock('@testing-library/react-native', ...) 삭제

// 경고 필터링
const originalConsoleWarn = console.warn;
const originalConsoleError = console.error;

console.warn = (...args) => {
  const warningMessage = args[0] || '';
  
  const ignoredWarnings = [
    'ProgressBarAndroid has been extracted',
    'Clipboard has been extracted',
    'TurboModuleRegistry.getEnforcing',
    'DevMenu could not be found'
  ];
  
  if (!ignoredWarnings.some(warning => 
    typeof warningMessage === 'string' && warningMessage.includes(warning)
  )) {
    originalConsoleWarn(...args);
  }
};

console.error = (...args) => {
  const errorMessage = args[0] || '';
  
  const ignoredErrors = [
    'act(',
    'inside a test was not wrapped in act',
    'unmounted component',
    '감정 로드 오류',
    '챌린지 데이터 로딩 오류'
  ];
  
  if (!ignoredErrors.some(error => 
    typeof errorMessage === 'string' && errorMessage.includes(error)
  )) {
    originalConsoleError(...args);
  }
};

// 다른 모킹들
jest.mock('react-native-safe-area-context', () => ({
  SafeAreaProvider: ({ children }) => children,
  SafeAreaView: ({ children }) => children,
  useSafeAreaInsets: () => ({ top: 0, right: 0, bottom: 0, left: 0 }),
}));

jest.mock('@react-navigation/native', () => ({
  useNavigation: () => ({
    navigate: jest.fn(),
    goBack: jest.fn(),
  }),
}));

// 타이머 모킹
jest.useFakeTimers();